import React from 'react';
import ReactDOM from 'react-dom/client'; // React 18 createRoot .
import App from './App'; // App.jsx  .

// React 18 createRoot     .
//  (Concurrent Mode)    .
const root = ReactDOM.createRoot(document.getElementById('root'));

// 'root'  App  .
// <React.StrictMode>       .
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);

